import {createElement} from '../render.js';
import { destinations } from '../data/static-data.js';
import { offersByTypes } from '../data/static-data.js';
import dayjs from 'dayjs';
import dur from 'dayjs/plugin/duration';

dayjs.extend(dur);

function calcDuration(start, end) {
  const duration = dayjs.duration(end.diff(start));
  if (duration.as('minutes') < 60) {
    return `${duration.format('m')}M`;
  } else if (duration.as('days') < 1) {
    return `${duration.format('H')}H ${duration.format('m')}M`;
  } else {
    return `${duration.format('D')}D ${duration.format('H')}H ${duration.format('m')}M`;
  }
}

function createPointTemplate(point) {
  const offers = offersByTypes.find((offer) => offer.type === point.type).offers;
  const selectedOffers = point.offers.map((selectedOfferId) => offers.find((offer) => offer.id === selectedOfferId));

  const destination = destinations.find((dest) => point.destination === dest.id);

  const start = dayjs(point.date_from);
  const end = dayjs(point.date_to);

  const duration = calcDuration(start, end);

  return `
  <li class="trip-events__item">
    <div class="event">
      <time class="event__date" datetime="${point.date_from}">${start.format('MMM DD')}</time>
      <div class="event__type">
        <img class="event__type-icon" width="42" height="42" src="img/icons/${point.type}.png" alt="Event type icon">
      </div>
      <h3 class="event__title">${point.type} ${destination.name}</h3>
      <div class="event__schedule">
        <p class="event__time">
          <time class="event__start-time" datetime="${point.date_from}">${start.format('HH:mm')}</time>
          &mdash;
          <time class="event__end-time" datetime="${point.date_to}">${end.format('HH:mm')}</time>
        </p>
        <p class="event__duration">${duration}</p>
      </div>
      <p class="event__price">
        &euro;&nbsp;<span class="event__price-value">${point.base_price}</span>
      </p>
      <h4 class="visually-hidden">Offers:</h4>
      <ul class="event__selected-offers">
        ${selectedOffers.map((selectedOffer) =>
    `<li class="event__offer">
          <span class="event__offer-title">${selectedOffer.title}</span>
          &plus;&euro;&nbsp;
          <span class="event__offer-price">${selectedOffer.price}</span>
        </li>`
  ).join('')}
      </ul>
      <button class="event__favorite-btn ${point.is_favorite && 'event__favorite-btn--active'}" type="button">
        <span class="visually-hidden">Add to favorite</span>
        <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
          <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
        </svg>
      </button>
      <button class="event__rollup-btn" type="button">
        <span class="visually-hidden">Open event</span>
      </button>
    </div>
  </li>`;
}

export default class PointView {

  constructor({point}) {
    this.point = point;
  }

  getTemplate() {
    return createPointTemplate(this.point);
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }

    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
